<template>
  <v-container>
    <v-card class="transparent" flat>
      <v-card-title>
        <h2 class="mx-auto upper">{{ this.$route.params.r }} Lectures</h2>
      </v-card-title>
      <v-card-text class="mt-2" v-if="allLectures.length > 0">
        <v-sheet class="transparent my-2 mx-auto" max-width="400">
          <v-text-field
            v-model="st"
            append-icon="mdi-magnify"
            outlined
            dense
            label="Search..."
            @keyup="searchLecture"
          ></v-text-field>
        </v-sheet>
        <div
          class="text-center classlink pa-5 black--text"
          v-for="(l, i) in lectures"
          :key="i"
        >
          <h2 class="mb-2">{{ l.subject }}</h2>
          <v-btn dark fab small title="Play" :href="l.link" target="_blank">
            <v-icon>mdi-play</v-icon>
          </v-btn>
        </div>
      </v-card-text>
      <div class="text-center my-4" v-else>
        <v-progress-circular
          :rotate="360"
          :size="120"
          :width="10"
          indeterminate
          color="orange"
        ></v-progress-circular>
      </div>
    </v-card>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      st: "",
      lectures: [],
      allLectures: [],
    };
  },
  methods: {
    getClasses() {
      fetch(
        "https://script.google.com/macros/s/AKfycbyx5uQDZ6BKjLZ3CXF9rEVAvUssY3DafWpJDIyMar0T_vVroVz9EU4hNnFWm0q2j0I3/exec?type=" +
          this.$route.params.r
      )
        .then((res) => res.json())
        .then((data) => {
          this.lectures = data.routines;
          this.allLectures = data.routines;
        });
    },
    searchLecture() {
      this.lectures = this.allLectures.filter((l) =>
        l.subject.toLowerCase().includes(this.st.toLowerCase())
      );
    },
  },
  created() {
    this.getClasses();
  },
};
</script>

<style lang="scss" scoped>
.upper {
  text-transform: capitalize;
}
.classlink {
  border-radius: 15px;
  background: linear-gradient(145deg, #f0f0f0, #cacaca);
  box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
}
</style>